.auth-container .card0 .flex-column-reverse {
    min-height: 100vh;
}

body {
    overflow-y: auto!important;
}

#logo {
    width: 300px;
}

.py-5, 
.py-4 {
    padding-bottom: 1rem!important;
    padding-top: 0!important;
}

.mb-3 {
    margin-top: 0rem!important;
}

.my-5 {
    margin-top:-1.5rem!important;
}

.auth-container input,
.auth-container textarea,
.auth-container button {
    background-color: #fff;
    border-radius: 50px !important;
    padding: 10px 15px 10px 15px;
    /* width: 100%; */
    box-sizing: border-box;
    border: none !important;
    border: 1px solid gray !important;
    font-size: 1.4rem !important;
    font-weight: 400
}

.auth-container .form-check-input {
    padding: 8px!important;
}

.auth-container .form-check-label {
    font-size: 1.2rem !important;
}

.auth-container input:focus,
.auth-container textarea:focus {
    -moz-box-shadow: none !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    border: 1px solid #2d9180 !important;
    outline-width: 0;
    font-weight: 400
}

.auth-container button:focus {
    -moz-box-shadow: none !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    outline-width: 0
}

.auth-container .card {
    border-radius: 0;
    border: none;
    background-color: #FFF;
}

.auth-container .card1 {
    width: 50%;
    /*padding: 40px 30px 10px 30px*/
    margin-top:auto;
    margin-bottom: auto;
}

.auth-container .card2 {
    width: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.auth-container .heading {
    margin-bottom: 60px !important
}

.auth-container ::placeholder {
    color: #000 !important;
    opacity: 1
}

.auth-container :-ms-input-placeholder {
    color: #000 !important
}

.auth-container ::-ms-input-placeholder {
    color: #000 !important
}

.auth-container .form-control-label {
    font-size: 1.2rem;
    margin-left: 15px
}

.auth-container .msg-info {
    padding-left: 15px;
    margin-bottom: 30px
}

.auth-container .btn-color {
    border-radius: 50px;
    color: #fff;
    background-color: var(--color__text);
    padding: 15px;
    cursor: pointer;
    border: 1px solid gray !important;
    margin-top: 40px
}

.auth-container .btn-color:disabled {
    cursor: default;
    opacity: 0.7;
}

.auth-container a {
    color: #000
}

.auth-container a:hover {
    color: #000
}

.auth-container .bottom {
    width: 100%;
}

.auth-container .sm-text {
    font-size: 15px
}

@media screen and (max-width: 992px) {
    .auth-container .card0 .flex-column-reverse {
        min-height: 100%;
    }
    .auth-container .card1 {
        width: 100%;
        padding: 40px 30px 10px 30px;
        min-height: 86vh;
    }
    .auth-container .card2 {
        width: 100%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .auth-container .right {
        margin-top: 100px !important;
        margin-bottom: 100px !important
    }
}

@media screen and (max-width: 768px) {
    .auth-container .card0 .flex-column-reverse {
        min-height: 100%;
    }
    .auth-container .container {
        padding: 10px !important
    }
    .auth-container .card2 {
        padding: 50px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .auth-container .right {
        margin-top: 50px !important;
        margin-bottom: 50px !important
    }
}

.auth-container .phone-input {
    text-align: center;
    font-size: 3rem!important;
}

.auth-container .cursor {
    cursor: pointer;
}

.auth-container .password-container {
    position: relative;
}

.auth-container .password-container .eye-img {
    position: absolute;
    right: 1.5rem;
    top: 1.3rem;
    cursor: pointer;
    width: 1.6rem;
    opacity: 60%;
}